body {
    margin: 0;
    padding: 0;
    color: rgb(151, 151, 151);
    /* min-width: 1200px; */
}
#app {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    height: 100vh;
}
#main {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    /* margin-bottom: 200px; */
}
.el-collapse-item__header{
    font-size: 16px;
    height: 45px;
    line-height: 45px;
}
.el-collapse-item__arrow{
  line-height: 45px;
}
.el-collapse-item__header.is-active{
  font-weight: 600;
}
.el-collapse-item__content{
  padding-top: 5px;
  padding-bottom:5px;
}
.el-card__body{
  padding: 0;
}
.el-input__icon {
    color:rgb(151, 151, 151); 
    font-size: 20px;
    cursor: pointer;
    padding-right: 5px;
}
.el-upload{
    display: block;
}
.w-e-text-container{
    min-height:400px;
}
.enlarge{
    background: rgba(32,14,54,0.5);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 8888888;
}
.closeImg{
	position: absolute;
	right: 0;
	top: 0;
	color: white;
	font-weight: 600;
	font-size: 25px;
	z-index: 8888888;
	cursor: pointer;
}
.closeImg:hover{
	-webkit-transform:rotate(360deg);
    transform:rotate(360deg);
    -webkit-transition:-webkit-transform 1s linear;
    transition:-webkit-transform 1s linear;
    transition:transform 1s linear;
    transition:transform 1s linear, -webkit-transform 1s linear;
}
.enlargeImage{
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%,-50%);
	        transform: translate(-50%,-50%);
}
.el-tabs__item{
    font-size: 18px;
}

